				"html": "<p>This tutorial shows you how to add aliases to the terminal, tweaking unity and removing pre-installed bloatware.</p>\n<h2>Steps:</h2>\n<h3>Removing Bloatware</h3>\n<p>To remove all pre-installed bloatware because of privacy concerns or to keep your operating system minimal, checkout <a href=\"https://gist.github.com/ansell/61313400e26cd42289f8\">this</a> gist.</p>\n<h3>Aliases</h3>\n<p>You can create a temporary alias like this:</p>\n<pre><code>alias alias_name=\"command_to_run\"\n</code></pre>\n<p>However, when you close your shell session, this alias will cease to exist.</p>\n<p>To create a permanent alias you will need to create the <code>~/.bash_aliases</code> file using the command <code>touch ~/.bash_aliases</code>. Once you've opened this file with your text editor of choice, add a line at the bottom of the document, similar to the example above.</p>\n<p>To find out more, DigitalOcean has a great tutorial that can be found <a href=\"https://www.digitalocean.com/community/tutorials/an-introduction-to-useful-bash-aliases-and-functions\">here</a>.</p>\n<h3>Unity Tweak Tool</h3>\n<p>The Unity Tweak Tool provides users with tons of configuration options for tweaking the Unity Desktop.</p>\n<p>To install the Unity Tweak Tool type <code>sudo apt install unity-tweak-tool</code>, and to launch it, <code>unity-tweak-tool</code>.</p>\n<p><a href=\"http://www.techrepublic.com/blog/linux-and-open-source/six-must-have-ubuntu-unity-tweaks/\">Here</a> is a list of the six must-have Ubuntu Unity Tweaks.</p>\n<p><img src=\"//forum.freecodecamp.com/images/emoji/emoji_one/point_left.png?v=2\" alt=\":point_left:\" title=\":point_left:\"> Previous | <img src=\"//forum.freecodecamp.com/images/emoji/emoji_one/book.png?v=2\" alt=\":book:\" title=\":book:\"> Home <img src=\"//forum.freecodecamp.com/images/emoji/emoji_one/book.png?v=2\" alt=\":book:\" title=\":book:\"> | Next <img src=\"//forum.freecodecamp.com/images/emoji/emoji_one/point_right.png?v=2\" alt=\":point_right:\" title=\":point_right:\"><br>\n<a href=\"//forum.freecodecamp.com/t/download-and-install-ubuntu-desktop/18383\"><strong>Download and Install Ubuntu Desktop</strong></a> | <a href=\"//forum.freecodecamp.com/t/setting-up-ubuntu-for-programming/18388\"><strong>Table of Contents</strong></a> | <a href=\"//forum.freecodecamp.com/t/jazzing-up-the-terminal/18386\"><strong>Jazzing up the Terminal</strong></a></p>",
